___CSS_LOADER_EXPORT___.push([module.id, ".main-page[data-v-f75e664c]{min-height:90vh}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Inventario/Perfil.vue"],"names":[],"mappings":"AAoSA,4BACA,eACA","sourcesContent":["<template>\r\n    <v-container class=\"my-3 main-page\">\r\n        <v-card tile>\r\n            <v-card-title>\r\n                <v-btn tile @click=\"atras\" icon class=\"mr-2\">\r\n                    <v-icon>mdi-arrow-left</v-icon>\r\n                </v-btn>\r\n                Datos del Usuario\r\n            </v-card-title>\r\n            <v-divider></v-divider>\r\n            <v-row class=\"pa-4\" no-gutter>\r\n                <v-col cols=\"12\" sm=\"4\">\r\n                    <h4\r\n                        class=\"pb-0 grey--text text--darken-2 text-sm-start text-sm-end\"\r\n                    >\r\n                        Nombres:\r\n                    </h4>\r\n                </v-col>\r\n                <v-col cols=\"12\" sm=\"8\">\r\n                    <h4 class=\"pb-0 grey--text text--darken-4\">\r\n                        {{ datos.nombres }} \r\n                    </h4>\r\n                </v-col>\r\n\r\n\r\n                <v-col cols=\"12\" sm=\"4\">\r\n                    <h4\r\n                        class=\"pb-0 grey--text text--darken-2 text-sm-start text-sm-end\"\r\n                    >\r\n                      Apellidos:\r\n                    </h4>\r\n                </v-col>\r\n                <v-col cols=\"12\" sm=\"8\">\r\n                    <h4 class=\"pb-0 grey--text text--darken-4\">\r\n                       {{ datos.apellidos }}\r\n                    </h4>\r\n                </v-col>\r\n\r\n                <v-col cols=\"12\" sm=\"4\">\r\n                    <h4\r\n                        class=\"pb-0 grey--text text--darken-2 text-sm-start text-sm-end\"\r\n                    >\r\n                        Correo electronico:\r\n                    </h4>\r\n                </v-col>\r\n                <v-col cols=\"12\" sm=\"8\">\r\n                    <h4 class=\"pb-0 grey--text text--darken-4\">\r\n                        {{ datos.email }}\r\n                    </h4>\r\n                </v-col>\r\n\r\n                <!--<v-col cols=\"12\" sm=\"4\">\r\n                    <h4\r\n                        class=\"pb-0 grey--text text--darken-2 text-sm-start text-sm-end\"\r\n                    >\r\n                        Rol de usuario:\r\n                    </h4>\r\n                </v-col>\r\n                <v-col cols=\"12\" sm=\"8\">\r\n                    <h4 class=\"pb-0 grey--text text--darken-4\">\r\n                        {{ datos.rol_name }}\r\n                    </h4>\r\n                </v-col> -->\r\n\r\n                <v-col cols=\"12\" sm=\"4\">\r\n                    <h4\r\n                        class=\"pb-0 grey--text text--darken-2 text-sm-start text-sm-end\"\r\n                    >\r\n                        Contraseña:\r\n                    </h4>\r\n                </v-col>\r\n                <v-col cols=\"12\" sm=\"8\">\r\n                    <v-btn\r\n                        @click=\"dialog_change_pass = !dialog_change_pass\"\r\n                        small\r\n                        color=\"primary\"\r\n                        >Cambiar contraseña</v-btn\r\n                    >\r\n                </v-col>\r\n            </v-row>\r\n        </v-card>\r\n       <TablaBienesRegistrados /> \r\n\r\n        <!--\r\n        <v-card tile>\r\n            <v-card-title> Areas Asignadas </v-card-title>\r\n            <v-divider></v-divider>\r\n            <v-expansion-panels\r\n                focusable\r\n                accordion\r\n                tile\r\n                v-model=\"areas_details\"\r\n            >\r\n                <v-expansion-panel v-for=\"(item, i) in datos.oficinas\" :key=\"i\">\r\n                    <v-expansion-panel-header>\r\n                        <strong class=\"primary--text\">{{ item.nombre }}</strong>\r\n                    </v-expansion-panel-header>\r\n\r\n                    <v-expansion-panel-content>\r\n                        <template v-if=\"areas_listas\">\r\n                            <v-list subheader two-line>\r\n                                <v-subheader inset>\r\n                                    Areas asignadas a\r\n                                    {{ datos.nombres }}\r\n                                </v-subheader>\r\n\r\n                                <v-list-item\r\n                                    v-for=\"(area, index) in item.areas\"\r\n                                    :key=\"index\"\r\n                                >\r\n                                    <v-list-item-avatar>\r\n                                        <v-icon class=\"warning\" dark>\r\n                                            mdi-office-building-marker\r\n                                        </v-icon>\r\n                                    </v-list-item-avatar>\r\n\r\n                                    <v-list-item-content>\r\n                                        <v-list-item-title\r\n                                            v-text=\"area.nombre\"\r\n                                        ></v-list-item-title>\r\n\r\n                                        <v-list-item-subtitle>\r\n                                            {{ area.responsables }}\r\n\r\n                                            <v-progress-linear\r\n                                                :value=\"\r\n                                                    (area.inventarios * 100) /\r\n                                                    area.bienesk\r\n                                                \"\r\n                                                height=\"15\"\r\n                                            >\r\n                                                <small>\r\n                                                    {{\r\n                                                        (area.inventarios *\r\n                                                            100) /\r\n                                                        area.bienesk\r\n                                                    }}\r\n                                                    %\r\n                                                </small>\r\n                                            </v-progress-linear>\r\n                                        </v-list-item-subtitle>\r\n                                    </v-list-item-content>\r\n\r\n                                   \r\n                                </v-list-item>\r\n                            </v-list>\r\n                        </template>\r\n\r\n                        <template v-else>\r\n                            <div class=\"text-center py-5\">\r\n                                <v-progress-circular\r\n                                    indeterminate\r\n                                    color=\"primary\"\r\n                                ></v-progress-circular>\r\n                            </div>\r\n                        </template>\r\n                    </v-expansion-panel-content>\r\n                </v-expansion-panel>\r\n            </v-expansion-panels>\r\n        </v-card>\r\n    -->\r\n\r\n        <v-dialog\r\n            v-model=\"dialog_change_pass\"\r\n            persistent\r\n            :overlay=\"false\"\r\n            max-width=\"400px\"\r\n        >\r\n            <v-card>\r\n                <v-card-title class=\"text-h5\">\r\n                    Cambiar contraseña\r\n                </v-card-title>\r\n\r\n                <v-form\r\n                    class=\"px-5 py-4\"\r\n                    ref=\"form\"\r\n                    v-model=\"valid\"\r\n                    lazy-validation\r\n                >\r\n                    <v-text-field\r\n                        v-model=\"password\"\r\n                        :append-icon=\"show_password ? 'mdi-eye' : 'mdi-eye-off'\"\r\n                        :rules=\"[rules.required, rules.min]\"\r\n                        :type=\"show_password ? 'text' : 'password'\"\r\n                        label=\"Nueva Contraseña\"\r\n                        counter\r\n                        @click:append=\"show_password = !show_password\"\r\n                    ></v-text-field>\r\n\r\n                    <v-btn\r\n                        :disabled=\"!valid\"\r\n                        color=\"primary\"\r\n                        class=\"mt-1\"\r\n                        block\r\n                        :loading=\"loading_btn\"\r\n                        @click=\"ChangePassword()\"\r\n                    >\r\n                        Guardar Contraseña\r\n                    </v-btn>\r\n\r\n                    <v-btn\r\n                        color=\"red\"\r\n                        class=\"mt-3\"\r\n                        block\r\n                        text\r\n                        @click=\"dialog_change_pass = !dialog_change_pass\"\r\n                    >\r\n                        Cancelar\r\n                    </v-btn>\r\n                </v-form>\r\n            </v-card>\r\n        </v-dialog>\r\n    </v-container>\r\n</template>\r\n\r\n<script>\r\nimport Layout from \"@/Layouts/InventarioLayout\";\r\nimport TablaBienesRegistrados from \"./Components/Tablas/TablaBienesRegistrados.vue\";\r\n\r\nexport default {\r\n    components: { TablaBienesRegistrados },\r\n    props: {\r\n        datos: Object,\r\n    },\r\n    layout: Layout,\r\n    data: () => ({\r\n        loading_btn: false,\r\n        show_password: false,\r\n        password: \"\",\r\n        valid: true,\r\n\r\n        dialog_change_pass: false,\r\n        areas_details: 0,\r\n        areas_listas: false,\r\n\r\n        rules: {\r\n            required: (value) => !!value || \"Requerido.\",\r\n            min: (v) => v.length >= 4 || \"Min 4 caracteres\",\r\n            email: (v) => /.+@.+\\..+/.test(v) || \"Formato incorrecto\",\r\n        },\r\n    }),\r\n    methods: {\r\n        async getInfoAreas(oficina) {\r\n            let res = await axios.get(\r\n                \"/get-data/areas/all-info/\" + oficina.id + \"/\" + this.datos.id\r\n            );\r\n\r\n            return res.data.datos;\r\n        },\r\n\r\n        async getInformacionArea() {\r\n            for (let i = 0; this.datos.oficinas.length > i; i++) {\r\n                this.datos.oficinas[i].areas = await this.getInfoAreas(\r\n                    this.datos.oficinas[i]\r\n                );\r\n            }\r\n\r\n            this.areas_listas = true;\r\n        },\r\n\r\n        async ChangePassword() {\r\n            if (this.$refs.form.validate()) {\r\n                try {\r\n                    this.loading_btn = true;\r\n                    let res = await axios.post(\"/inventario/update-password\", {\r\n                        password: this.password,\r\n                    });\r\n\r\n                    this.dialog_change_pass = false;\r\n                    this.loading_btn = false;\r\n                } catch (error) {\r\n                    this.mensaje = error.response.data.error;\r\n                    this.loading_btn = false;\r\n                }\r\n            }\r\n        },\r\n        atras() {\r\n            window.history.back();\r\n        },\r\n    },\r\n    watch: {},\r\n\r\n    async created() {\r\n    \r\n        if (this.datos) {\r\n            // await this.getInformacionArea();\r\n        }\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.main-page {\r\n    min-height: 90vh;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
